/* Global Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #1a1a1a; /* Consistent dark background */
  color: #f0f0f0;
}

/* Contact Container */
.contact {
  padding: 50px 20px;
  background-color: #333; /* Dark background for uniformity */
  border-radius: 15px;
  max-width: 600px;
  margin: 50px auto;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  text-align: center;
}

/* Header Animation */
.fade-in {
  font-size: 2rem;
  color: #00ff88; /* Accent color */
  opacity: 0;
  animation: fadeIn 1.5s ease-in-out forwards;
  margin-bottom: 20px;
}

/* Contact List Styles */
.contact-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px; /* Uniform spacing */
  opacity: 0;
  animation: slideIn 1.5s ease-in-out forwards;
  animation-delay: 0.5s;
}

/* Contact List Links */
.contact-list a {
  text-decoration: none;
  color: #f0f0f0;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  transition: transform 0.3s ease, color 0.3s ease;
}

/* Hover Effects */
.contact-list a:hover {
  transform: translateY(-5px);
  color: #00ff88; /* Accent color on hover */
}

/* Icon Styles */
.icon, .custom-icon {
  margin-right: 10px;
  font-size: 24px;
}

/* Custom Icons (LeetCode, Coding Ninjas) */
.custom-icon {
  width: 24px;
  height: 24px;
  object-fit: cover;
}

/* Fade-in Keyframes */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Slide-in Keyframes */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
