/* Global Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #1a1a1a; /* Consistent dark background */
  color: #f0f0f0;
}

/* Projects Card Styles */
.projects-card {
  width: 300px;
  margin: 20px;
  border-radius: 15px;
  overflow: hidden;
  background-color: #2c2c2c; /* Dark background for cards */
  color: #fff;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}

/* Card Hover Effects */
.projects-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.4);
}

/* Image Styles */
.projects-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

/* Image Hover Effect */
.projects-image.zoom-in:hover {
  transform: scale(1.05);
}

/* Card Body */
.card-body {
  padding: 20px;
  background-color: #2c2c2c;
}

/* Title */
.card-title {
  font-size: 1.3rem;
  margin-bottom: 10px;
  color: #00ff88; /* Accent color */
}

/* Subtitle */
.card-subtitle {
  font-size: 1.1rem;
  color: #aaa;
  margin-bottom: 10px;
}

/* Card Text */
.card-text {
  font-size: 1rem;
  line-height: 1.6;
  color: #ddd;
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  opacity: 0;
  animation: fadeIn 1.5s ease-in-out forwards;
}

/* Zoom-in Animation */
@keyframes zoomIn {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.05);
  }
}

.zoom-in {
  animation: zoomIn 0.3s ease-in-out forwards;
}
