/* Container for the About Section */
.about-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 50px;
  background: linear-gradient(45deg, #1a1a1a, #2c3e50); /* Gradient background */
}

/* About Card */
.about-card {
  background: rgba(255, 255, 255, 0.9); /* Slight transparency */
  border-radius: 15px;
  padding: 40px;
  max-width: 800px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  text-align: left;
  color: #333; /* Text color */
}

/* Title Styling */
.about-card h2 {
  font-size: 2rem;
  color: #009688;
  margin-bottom: 20px;
}

/* Section Title Styling */
.about-section h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #222;
}

/* Paragraph Styling */
.about-card p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #444;
  margin-bottom: 15px;
}

/* Icon Styling */
.about-icon {
  font-size: 1.5rem;
  margin-right: 10px;
  color: #009688; /* Accent color */
}

/* Intro and Closing Text */
.intro-text, .closing-text {
  font-size: 1.2rem;
  font-style: italic;
  color: #555;
}

/* Section container */
.about-section {
  margin-top: 20px;
  padding-left: 30px;
  border-left: 3px solid #009688;
}
